@import "https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap";
:root {
  --unit-tiny: 8px;
  --unit-sm: 16px;
  --unit-md: 26px;
  --unit-lg: 42px;
  --unit-xl: 67px;
}

@media print {
  :root {
    --unit-tiny: 8px;
    --unit-sm: 14px;
    --unit-md: 21px;
    --unit-lg: 26px;
    --unit-xl: 36px;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-size: var(--unit-sm);
  font-weight: 400;
}

a {
  color: #666;
}

.pdf-download {
  position: absolute;
  top: 0;
  right: 0;
}

@media print {
  .pdf-download {
    display: none;
  }
}

.pdf-download__link {
  padding: var(--unit-tiny);
  background: #efefef;
}

.page-container {
  margin: var(--unit-lg) auto;
  max-width: 90%;
  font-family: Roboto, Arial, sans-serif;
}

@media (width >= 700px) {
  .page-container {
    max-width: 768px;
  }
}

@media print {
  .page-container {
    max-width: 95%;
  }
}

.header {
  margin-bottom: var(--unit-md);
  flex-wrap: wrap;
  align-items: baseline;
  display: flex;
}

.header__title {
  margin-right: var(--unit-sm);
  font-size: var(--unit-xl);
  line-height: 1;
}

.header__subtitle {
  margin-bottom: var(--unit-sm);
  font-size: var(--unit-lg);
  font-weight: 300;
  line-height: 1;
}

.header__contact-list {
  flex-basis: 100%;
  list-style: none;
  display: flex;
}

@media print {
  .header__contact-list {
    flex: 1;
    justify-content: flex-end;
  }
}

.header__contact-list__item {
  font-size: var(--unit-sm);
  margin-right: var(--unit-md);
}

@media print {
  .header__contact-list__item {
    margin-right: var(--unit-sm);
  }
}

.resume-section {
  margin-bottom: var(--unit-md);
}

.resume-section__title {
  font-size: var(--unit-md);
  margin-bottom: var(--unit-sm);
  font-weight: 300;
}

@media print {
  .resume-section__title {
    font-weight: 400;
  }
}

.resume-section__body {
  font-size: var(--unit-sm);
  line-height: 1.4;
}

.resume-section__body--two-columns {
  row-gap: var(--unit-md);
  column-gap: var(--unit-lg);
  grid-template-columns: 1fr;
  display: grid;
}

.resume-section__body--two-columns .section-detail {
  margin-bottom: 0;
}

@media (width >= 700px) {
  .resume-section__body--two-columns {
    grid-template-columns: 1fr 1fr;
  }
}

@media print {
  .resume-section__body--two-columns {
    grid-template-columns: 1fr 1fr;
  }
}

.section-detail {
  row-gap: var(--unit-sm);
  margin-bottom: var(--unit-md);
  grid-template-rows: min-content;
  grid-template-columns: 3fr 2fr;
  display: grid;
}

.section-detail__title {
  font-size: var(--unit-sm);
  grid-column: 1 / 3;
  font-weight: 500;
}

@media (width >= 700px) {
  .section-detail__title {
    grid-column: 1 / 2;
  }
}

@media print {
  .section-detail__title {
    grid-column: 1 / 2;
  }
}

.section-detail__subtitle {
  grid-column: 1 / 3;
  font-weight: 500;
}

@media (width >= 700px) {
  .section-detail__subtitle {
    text-align: right;
    grid-column: 2 / 3;
  }
}

@media print {
  .section-detail__subtitle {
    text-align: right;
    grid-column: 2 / 3;
  }
}

.section-detail__body {
  grid-column: 1 / 3;
}

.experience-list {
  gap: var(--unit-sm);
  grid-template-columns: auto;
  display: grid;
}

@media (width >= 700px) {
  .experience-list {
    grid-template-columns: minmax(auto, 200px) auto;
  }
}

@media print {
  .experience-list {
    grid-template-columns: minmax(auto, 200px) auto;
  }
}

.experience-list__name {
  font-weight: 400;
}

.rating {
  align-items: center;
  display: flex;
}

.rating__label {
  margin-right: var(--unit-sm);
  flex: 0 120px;
}

.rating__value {
  flex: 1;
}

.labeled-link__label {
  display: none;
}

@media (width >= 700px) {
  .labeled-link__label {
    display: inline-block;
  }
}

.unstyled-list {
  list-style-type: none;
}

.indented-list {
  margin-left: var(--unit-md);
}

.value-meter {
  background: #ddd;
  border-radius: 6px;
  width: 100px;
  height: 12px;
  position: relative;
  overflow: hidden;
}

.value-meter:before {
  content: "";
  background: #888;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.value-meter--1:before {
  width: 10%;
}

.value-meter--2:before {
  width: 20%;
}

.value-meter--3:before {
  width: 30%;
}

.value-meter--4:before {
  width: 40%;
}

.value-meter--5:before {
  width: 50%;
}

.value-meter--6:before {
  width: 60%;
}

.value-meter--7:before {
  width: 70%;
}

.value-meter--8:before {
  width: 80%;
}

.value-meter--9:before {
  width: 90%;
}

.value-meter--10:before {
  width: 100%;
}

.value-meter--10:after {
  content: "â˜…";
  color: #eee;
  font-size: 9px;
  line-height: 1;
  position: absolute;
  top: 2px;
  right: 3px;
}

